.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.centered {
    display: flex;
    justify-content: center;
    height: 100%;
}

.rowContent {
    display: flex;
    flex-direction: row;
}
.dimInput {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
}
.saveDimInput {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    background-color: #FFFFFF;
    width: 100px;
}
.saveDimButton {
    display: flex;
    justify-content: flex-start;
}
.rowOutlineConfig {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.card {
    background-color: #282c34;
}
